<template>
    <v-container>
        <v-row justify="center">
            <v-col sm="3">
                <v-card class="pa-8" outlined>
                    <v-card-title>로그인</v-card-title>
                    <v-text-field v-model="user.email" label="ID" clearable prepend-icon="mdi-email" />
                    <v-text-field v-model="user.password" type="password" label="PW" clearable
                        prepend-icon="mdi-lock-outline" />
                    <v-card-actions>
                        <v-btn @click="Login" color="primary" outlined>Login</v-btn>
                    </v-card-actions>
                </v-card>

            </v-col>
        </v-row>
        <v-row justify="center">
            <v-col sm="3">
              <div>{{ip}}</div>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
    data: () => ({
        user: {
            email: null,
            password: null
        },
        ip: '',
       

    }),
    created() {

        this.getIP()
        this.ip = this.$store.getters.getIp
    },
    methods: {
        Login: function () {
            this.$store.commit('login', 'this is a token-data')
            this.$router.push('/')
        },
        getIP: function () {
        
        this.$axios.get('/api').then((res) => {
            this.$store.commit('setIP', res.data)
            console.log(res.data)
             
            }) 
        }
    }
}
</script>

